import Vue from "vue";
import App from "./App.vue";
import router from "./router";

import ElementUI from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
Vue.use(ElementUI);
Vue.config.productionTip = false;

Vue.directive("nav", (n, v) => {
  if (v.value % 2 != 0) {
    n.style.backgroundColor = "salmon";
    n.style.color = "white";
  }
});

new Vue({
  router,
  render: (h) => h(App),
}).$mount("#app");
